export default {
	data() {
		return {
			poneNumber: '',
			show: false,
			shopshow: false,
			shopid: 0,
			isTeam: 0,
			isShow: false
		}
	},
	// 每次页面显示时从缓存中取出已保存的登录信息
	onShow: function() {
		var that = this
		var phone = uni.getStorageSync('phone')
		console.log(phone)
		this.poneNumber = this.passwordTl(phone)
		// 判断用户是否登录
		if (uni.getStorageSync('phone')) {
			this.show = true
			console.log(that.show)
		} else {
			this.show = false
		}
		// 是否是代理商
		console.log(phone)
		uni.request({
			url: this.$bashUrl + '/index.php/home/Api/is_team/',
			method: "POST",
			data: {
				phone: phone
			},
			success: res => {
				console.log(res)
				this.isTeam = res.data
			}
		})


		// 判断用户是否是商家用户			
		uni.request({
			url: this.$bashUrl + '/index.php/home/Api/is_store/',
			method: "POST",
			data: {
				phone: phone
			},
			success(res) {
				console.log(res.data)
				if (res.data != 0) {
					console.log('执行商家')
					that.shopshow = true
					that.shopid = res.data
				}
			},
			fail(err) {
				console.log('获取错误')
			}
		})
	},
	onLoad: function(options) {
		// 取出登录或注册全局用户信息
		var phone = uni.getStorageSync('phone');
		// 默认取得手机号码
		this.poneNumber = this.passwordTl(phone)
	},
	methods: {
		toWallet() {
			uni.navigateTo({
				url: "/pages/wallet/wallet"
			})
		},
		exit() {



			console.log('exit')
		},
		close() {
			this.isShow = false
		},
		toPrivate() {
			uni.navigateTo({
				url: "/pages/private/private"
			})
		},
		toHongbao() {
			uni.navigateTo({
				url: "../qrCode/qrCode"
			})
		},
		//分销商入口
		toMyteam() {
			// uni.navigateTo({
			// 	url:'../toAgent/toAgent'
			// })
			// 如果是分销商进入团队
			let phone = uni.getStorageSync('phone')
			if (phone) {

				if (this.isTeam != "0") {
					uni.navigateTo({
						url: '../myTeam/myTeam'
					})
				} else {
					//如果不是进入注册
					uni.navigateTo({
						url: '../toAgent/toAgent'
					})

				}
			} else {
				uni.showToast({
					title: "请先登录",
					icon: "none"
				})
				setTimeout(() => {
					uni.navigateTo({
						url: '/pages/sigUp/sigUp'
					})
				}, 1500)

			}
		},
		passwordTl: function(tl) {
			let phone = tl.toString();
			// 默认处理字符串电话号码，不接受数字号码
			var reg = /^(\d{3})\d{4}(\d{4})$/
			return (phone.substr(0, 3) + '****' + phone.substr(7))
		},
		todindan() {
			uni.switchTab({
				url: '/pages/reserve/reserve'
			});
		},
		ToshopSide: function() {
			// 商家端入口
			uni.navigateTo({
				url: '/pages/BusinessSide/index/index?shopid=' + this.shopid,
			})
		},
		Tosigup: function() {
			// 注册页面入口
			uni.navigateTo({
				url: '/pages/sigUp/sigUp'
			})
		},
		login() {
			uni.navigateTo({
				url: '/pages/sigUp/sigUp'
			})
		},
		tixianList() {
			// 清除缓存,并重新跳转到该页面
			uni.clearStorageSync()
			uni.showToast({
				title: '已退出账户',
				icon: 'none'
			});
			setTimeout(function() {
				uni.navigateTo({
					url: '/pages/sigUp/sigUp'
				})
			}, 1000);
		}
	}
}
